//   
export default (() => {
  let data = {}; //  
  let subscribes = {}; // 

  //    
  const get = (key) => {
    return data[key];
  };

  //  
  const init = (key, value) => {
    const oldValue = data[key];
    if (oldValue == undefined) {
      data[key] = value;
    } else {
      throw new Error(`key - ${key} is already init`);
    }
    return value;
  };

  //    
  const set = (key, value) => {
    const oldValue = data[key];
    if (oldValue == undefined) {
      throw new Error(`key - ${key} is not init`);
    }
    data[key] = value;
    subscribes[key] && subscribes[key](oldValue, value);
    return value;
  };

  //    
  const remove = (key) => {
    delete data[key];
  };

  const subscribe = (key, callback) => {
    subscribes[key] = callback;
  };

  const showData = () => {
    console.dir(data);
  };

  const showSubscribes = () => {
    console.dir(subscribes);
  };

  //    
  return {
    init,
    get,
    set,
    remove,
    subscribe,
    showData,
    showSubscribes,
  };
})();
