import "./src/styles/styles.scss";
import {
  initControlsForDebug,
  initStore,
  initMap,
  initSearch,
  initSubscribers,
  initHandlers,
  initInteractions,
  initMenu,
} from "./src/inits";
import store from "./src/store";

(async function main() {
  // ,     webview (Android)
  try {
    window.isAndroid = Android && true;
  } catch {
    window.isAndroid = false;
  }
  //         
  await initStore();
  //  
  await initSearch();
  //  
  await initMenu();
  //        
  process.env.DEBUG && initControlsForDebug();
  //      
  await initMap();
  //  Select, Overlay, Modify
  await initInteractions();
  //  
  await initHandlers();
  //   
  initSubscribers();
  //      
  store.get("map").getTarget().style.display = "";
})();
